@import url('https://fonts.googleapis.com/css?family=Baloo+2:400,800&display=swap');


*,
*::before,
*::after {
    box-sizing: border-box;
}

.bar-container {
    display: flex;
    justify-content: space-between;
    /* Coloca el título a la izquierda y los íconos a la derecha */
    align-items: center;
    width: 90%;
    /* Ancho de la barra */
    margin: 0 auto;
    /* Centra horizontalmente la barra */
    color: #FAFAF8;
    padding-left: 15px;
    padding-right: 15px;
    gap: 1.5rem;
    z-index: 1;
    height: 6vh;
    position: relative;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}

.bar-container::after {
    content: '';
    display: block;
    width: 100%;
    /* Ocupa el 90% del ancho de la barra */
    height: 2px;
    /* Altura del separador */
    background-color: #333;
    /* Color del separador */
    margin: 0 auto;
    /* Centra el separador horizontalmente */
    position: absolute;
    bottom: -2px;
    /* Coloca el separador justo debajo de la barra */
    left: 0;
    right: 0;
}

.icon {
    /*color: #FAFAF8;*/
    color: #333;
}

.title {
    font-family: 'Baloo 2', cursive;
    font-weight: bold;
    color: #333;
    text-align: center;
    font-size: 2rem;
    /* Aumenta el tamaño del texto del título */
}

.tiles {
    border: 2px solid #333;
    /* Borde por defecto */
    background-color: transparent;
    /* Fondo transparente */
    color: #333;
    /* Texto por defecto */
    border-radius: 8px;
    /* Bordes redondeados */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90px;
    /* Ancho */
    height: 90px;
    /* Altura */
    cursor: pointer;
    font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
    font-weight: bold;
    font-size: 1.2rem;
    /* Tamaño del texto */
    transition: all 0.3s ease;
}

.tiles.completed-tile {
    border: 2px solid #4caf50;
    /* Borde verde */
    background-color: #d4edda;
    /* Fondo verde clarito */
    color: #FAFAF8;
    /* Texto del color de fondo de la modal */
}

.tiles:hover {
    background-color: #333;
    /* Fondo gris oscuro al pasar el mouse */
    color: #fff;
    /* Texto blanco al pasar el mouse */
}

.tiles-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    max-height: 48vh;
    overflow: auto;
    word-wrap: break-word;
    white-space: normal;
    padding: 1rem;

}

.calendar-grid {
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-title {
    font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    color: #333;
    text-transform: uppercase;
    font-weight: 800 !important;
    margin-top: 20px !important;
    padding-bottom: 20px !important;
    border-bottom: 1px solid #333;
    width: 90%;
}

.box-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    width: 40vw;
    background-color: #FAFAF8;
    border: none;
    outline: none;
    padding: 1rem;
    max-height: 70vh;
    overflow: auto;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1), 0px 4px 8px rgba(0, 0, 0, 0.15);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    word-wrap: break-word;
    white-space: normal;
}

.tiles-container::-webkit-scrollbar {
    width: 5px;
    /* Width of the vertical scrollbar */
    height: 5px;
    /* Height of the horizontal scrollbar */
}

/* Handle */
.tiles-container::-webkit-scrollbar-thumb {
    background: #333;
    /* Scrollbar color */
    border-radius: 10px;
    /* Rounded corners for the scrollbar */
}

/* Handle on hover */
.tiles-container::-webkit-scrollbar-thumb:hover {
    background: #333;
}

@media only screen and (max-width: 900px) {
    .box-container {
        width: 60vw;
    }

}

@media only screen and (max-width: 650px) {
    .box-container {
        width: 80vw;
    }
}

@media only screen and (max-width: 750px) {
    .title {
        font-size: 1rem;
    }
}

.green-tile {
    background-color: #9DBC98;
    /* Ajusta el color según tu diseño */
}

.navbar-separator {
    width: 90%;
    /* Ocupa el 90% del ancho de la barra */
    height: 2px;
    /* Altura del separador */
    background-color: #333;
    /* Color del separador */
    margin: 0 auto;
    /* Centra el separador horizontalmente */
}

.icons-container {
    display: flex;
    align-items: center;
    gap: 1rem;
    /* Espaciado entre los íconos */
}